@import url(https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap);
body {
  margin: 0;
  font-family: "Inter", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #111;
  cursor: url(data:image/png;base64,), auto !important;
}

code {
  font-family: "Inter", sans-serif;
}

.mb-0 {
  margin-bottom: 0;
}
.mt-0 {
  margin-top: 0;
}

.m-0 {
  margin: 0;
}

.pb-10 {
  padding-bottom: 10px;
}

.intro-flex{display:flex;max-width:1440px;margin:0 auto;align-items:center;padding:0 4%}.intro-flex div{flex:1 1}.intro-text{padding:2%}.intro-text button{margin-top:10px}.intro-img{overflow:hidden;padding:0 2%}.intro-img img{width:100%}h1{font-size:80px;color:#376ac0;margin:0;line-height:1;letter-spacing:-0.07em}h2{color:#376ac0;font-weight:700;font-size:3rem;line-height:1.3;letter-spacing:-1px}h3{font-size:20px;font-weight:400;line-height:1.6;color:#666}p{font-size:16px;color:#696969;margin:0}a{color:#f76239;font-weight:bold;text-decoration:none;position:relative;cursor:url(data:image/png;base64,),auto !important}a::before{content:"";position:absolute;width:100%;height:2px;bottom:0;left:0;background-color:#f76239;visibility:hidden;transform:scaleX(0);transition:all 0.3s ease-in-out 0s}a:hover::before{visibility:visible;transform:scaleX(1)}button{text-transform:uppercase;padding:0 5.5rem;background-color:white;height:3rem;font-weight:bold;color:#376ac0;border:2px solid #376ac0;transition:color 0.45s cubic-bezier(0.785, 0.135, 0.15, 0.86);transform:perspective(1px) translateZ(0);cursor:url(data:image/png;base64,),auto !important}button:before{content:"";position:absolute;z-index:-1;top:0;left:0;right:0;bottom:0;background-color:#376ac0;transform:scaleX(0);transform-origin:right center;transition:transform 0.45s cubic-bezier(0.785, 0.135, 0.15, 0.86)}button:hover{color:white;text-decoration:none}button:hover:before{transform-origin:left center;transform:scale(1)}button:focus{outline:0}.nowrap{white-space:nowrap}.work-container{text-align:center;max-width:1024px;margin:0 auto;padding:0 4%}.showcase-container{padding-top:12px;display:grid;grid-template-columns:repeat(auto-fill, minmax(340px, 1fr));grid-gap:24px;gap:24px}.showcase-container .work{position:relative;border-radius:5px;text-align:left;border:1px solid #eaeaea;padding:24px}.showcase-container .techlist{display:flex;align-items:center;justify-content:flex-start}.showcase-container .techlist img{max-width:100px;margin:0 20px 0 0}.showcase-container .techlist .tech{text-transform:uppercase;letter-spacing:1px;margin-right:3px;font-weight:bold}.showcase-container .image-container{max-height:180px;overflow:hidden;margin-bottom:10px;border-radius:5px}.showcase-container img{width:100%}.projects-container{text-align:center;max-width:1024px;margin:10% auto;padding:0 4%}.projects-grid{padding-top:24px;display:grid;grid-template-columns:repeat(auto-fill, minmax(320px, 1fr));grid-gap:#eaeaea;grid-gap:24px;gap:24px}.projects-grid .project{border:1px solid #eaeaea;padding:24px;border-radius:5px;text-align:left;flex:1 1;display:flex;flex-direction:column;transition:box-shadow 0.2s ease, border 0.2s ease}.projects-grid .project h3{font-weight:600;color:#111;font-size:1.2}.projects-grid .project a{margin:10px 0}.projects-grid .project:hover{transition:box-shadow 0.2s ease;box-shadow:0 8px 30px rgba(0,0,0,0.12);border:1px solid transparent}.navbar{z-index:10000;max-width:1440px;margin:0 auto;padding:20px 6% 10px}.navbar .logo{width:75px}.gray-region{margin:10% auto;padding:6.25rem 0px;background-color:#fafafa;border-top:1px solid #eaeaea;border-bottom:1px solid #eaeaea}

@media only screen and (min-device-width: 375px) and (max-device-width: 812px){.intro-flex{flex-direction:column}.intro-flex .intro-text{order:2;margin-top:10px}.intro-flex .intro-text p{font-size:16px;font-weight:600}.intro-flex .intro-text button{width:100%}.intro-flex .intro-img{margin-top:90px}.navbar{position:fixed;width:100%;height:62px;padding:0;-webkit-backdrop-filter:saturate(180%) blur(20px);backdrop-filter:saturate(180%) blur(20px);background-color:rgba(255,255,255,0.7)}.navbar .logo{padding:10px 6% 10px;width:40px}h1{font-size:40px}h3{font-size:20px}}

